.reminder-block {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    align-items: center;
    display: flex;
    justify-content: center;
  
    ion-toggle {
      --background-checked: orange;
      margin: 0 auto;
    }
    .alarm {
      font-size: 30px;
    }
    h3 {
      margin-top: 5px;
      font-weight: 400;
    }
  }
  